import React from 'react';
import styled from 'styled-components';

const ProductSet = ({children}) => {
    return (
<>
<ProjectManage>{children}</ProjectManage>
<MainContent>
    
</MainContent>
</>
    );
};

export default ProductSet;

const ProjectManage = styled.div`
    font-size: 25px;
    margin: 20px auto;
    width: 90%;
`;


const MainContent = styled.div`
// border: solid 2px green;
width: 90%;
height: 800px;
background: white;
margin: 0 auto;
border-radius: 10px;
`