import React, { useState } from 'react';
import styled from 'styled-components';

const ProductSet = ({children}) => {
    const [modal,setModal] = useState(false);
    return (
<>
<ProjectManage>{children}</ProjectManage>
<MainContent>
    <MainTop>
        <input value="제목으로 검색해주세요"/>
        <AddProd onClick={()=>{setModal(!modal); console.log(modal)}}>+신규작품등록</AddProd>
    </MainTop>
    {
      modal === true
      ? <Modal>
            <ModalPop>
                <ModalTitle>
                    <p>신규작품 등록</p>
                    <p onClick={()=>{setModal(!modal)}}>x</p>
                </ModalTitle>
                <ProdTitle>
                    <span>*</span>작품제목:
                    <input value="ex)백작가의 망나니가 되었다."/>
                </ProdTitle>
            </ModalPop> 
        </Modal>
      : null
    }
</MainContent>
</>
    );
};

export default ProductSet;

const ProjectManage = styled.div`
    font-size: 25px;
    margin: 20px auto;
    width: 90%;
`;


const MainContent = styled.div`
    // border: solid 2px green;
    width: 90%;
    height: 800px;
    background: white;
    margin: 0 auto;
    border-radius: 10px;
`

const MainTop = styled.div`
    // border: solid 2px red;
    display: flex;
    justify-content: space-between;
    padding: 10px;
`

const AddProd = styled.div`
    padding: 10px 40px;
    font-size: 15px;
    // border: solid 2px blue;
    border-radius: 5px;
    background: skyblue;
    color: white;
    transition: .5s;
    cursor: pointer;
    &:hover {
        background: steelblue;
    }
`

const Modal = styled.div `
    width: 100vw; height: 100vh;
    background: rgba(0, 0, 0, 0.155);
    position: fixed;
    top: 0; left: 0;
`

const ModalPop = styled.div`
    position: fixed;
    top: 30%;
    left: 55%;
    transform: translate(-50%,-50%);
    background: white;
    width: 30%; height 20%;
`

const ModalTitle = styled.div`
    border-bottom: solid 1px silver;
    display: flex;
    padding: 5px 15px;
    align-items: center;
    height: 20%;
    font-size: 15px;
    >p:nth-child(1) {
        color: black;
    }
    >p:nth-child(2) {
        // border: solid 2px red;
        font-size: 30px;
        cursor: pointer;
    }
`

const ProdTitle = styled.div`
    // border: solid 2px red;
    height: 50%;
    width: 100%;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    >span{color: red}
    >input {
        margin-left: 10px;
        padding: 10px;
    }
`